<template>
  <div class="login-container">
    <!-- 导航栏 -->
    <van-nav-bar
      class="page-nav-bar"
      title="登录"
    >
      <van-icon
        name="arrow-left"
        slot="left"
        @click="$router.back()"
      />
    </van-nav-bar>
    <!-- 登录表单 -->
    <van-form
      @submit="onSubmit"
      ref='loginForm'
    >
      <van-field
        v-model="user.mobile"
        name="mobile"
        placeholder="请输入手机号"
        :rules="userFormRules.mobile"
        type="number"
        maxlength="11"
      >
        <i
          slot="left-icon"
          class="iconfont icon-shouji"
        ></i>
      </van-field>
      <van-field
        v-model="user.code"
        name="code"
        placeholder="请输入验证码"
        :rules="userFormRules.code"
        type="number"
        maxlength="6"
      >
        <i
          slot="left-icon"
          class="iconfont icon-yanzhengma"
        ></i>
        <template #button>
          <!-- time是时长，单位是毫秒 -->
          <van-count-down
            v-if="isCountDownShow"
            :time="60000"
            format="ss s "
            @finish='isCountDownShow=false'
          />
          <van-button
            v-else
            size="small"
            type="default"
            class="send-sms-btn"
            native-type="button"
            round
            @click="onSendSms"
          >获取验证码</van-button>
        </template>
      </van-field>
      <div class="login-btn-wrap">
        <van-button
          block
          type="info"
          native-type="submit"
          class="login-btn"
        >
          登录
        </van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
import { login, sendSms } from '@/api/user'
export default {
  name: 'LoginIndex',
  data () {
    return {
      user: {
        mobile: '18888888888',
        code: '246810'
      },
      // 控制倒计时的显示与隐藏
      isCountDownShow: false,
      // 登录表单验证规则
      userFormRules: {
        mobile: [{ required: true, message: '手机号不能为空' }, {
          pattern: /^1[3|5|7|8]\d{9}$/,
          message: '手机号格式错误'
        }],
        code: [{ required: true, message: '请输入验证码' }, {
          pattern: /^\d{6}$/,
          message: '验证码格式错误'
        }]
      }
    }
  },
  created () {

  },
  methods: {
    async  onSubmit () {
      // 1.获取表单数据
      const user = this.user
      // 2.表单验证
      this.$toast.loading({
        message: '登录中...',
        forbidClick: true, // 禁用背景点击
        duration: 0// 展示时长默认是2秒,如果为0则持续展示
      })
      // 3.提交表单请求登录
      try {
        const { data } = await login(user)
        this.$store.commit('setUser', data.data)
        this.$toast.success('登录成功')
        // 清除layout的缓存，让它重新渲染
        this.$store.commit('removeCachePage', 'LayoutIndex')
        // 登录成功跳回原来的页面
        this.$router.push(this.$route.query.redirect || '/')
        // this.$router.back()
      } catch (err) {
        if (err.response.status === 400) {
          this.$toast.fail('手机号或验证码错误')
        } else {
          this.$toast.fail('登录失败请稍后重试')
        }
      }
      // 4.根据请求响应结果处理后续操作
    },
    async  onSendSms () {
      // 1.校验手机号

      try {
        await this.$refs.loginForm.validate('mobile')
      } catch (err) {
        return console.log('验证失败', err)
      }
      // 2.验证通过显示倒计时
      this.isCountDownShow = true
      // 3.请求发送验证码
      try {
        await sendSms(this.user.mobile)
        this.$toast('发送成功')
      } catch (err) {
        // 发送失败关闭倒计时
        this.isCountDownShow = false
        if (err.response.status === 429) {
          this.$toast('请求频繁，请稍后重试')
        } else {
          this.$toast('发送失败，请稍后重试')
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.login-container {
  .iconfont {
    font-size: 37px;
  }
  .send-sms-btn {
    width: 152px;
    height: 46px;
    background: #ededed;
    border-radius: 23px;
    font-size: 22px;
    color: #666666;
  }
  .login-btn-wrap {
    padding: 53px 28px;
    .login-btn {
      background-color: #6db4fb;
      border: none;
    }
  }
}
</style>
